"use client"

import React from "react";
import {
  Table,
  TableHeader,
  TableBody,
  TableRow,
  TableHead,
  TableCell,
  TableCaption,
} from "@/components/ui/table";
import { Card, CardContent } from "@/components/ui/card";
import { Badge } from "@/components/ui/badge";
import { InsuranceProduct } from "@/lib/insuranceData";

interface ComparisonTableProps {
  data: InsuranceProduct[];
  insuranceType: string;
}

export default function ComparisonTable({ data, insuranceType }: ComparisonTableProps) {
  if (!data || data.length === 0) {
    return (
      <Card>
        <CardContent className="p-8 text-center">
          <p className="text-muted-foreground">Tidak ada data perbandingan untuk asuransi {insuranceType}.</p>
        </CardContent>
      </Card>
    );
  }

  const typeNames = {
    auto: "Kendaraan",
    health: "Kesehatan",
    life: "Jiwa",
    home: "Rumah"
  };

  return (
    <div className="space-y-6">
      <div className="overflow-x-auto">
        <Table>
          <TableCaption>
            Bandingkan opsi asuransi {typeNames[insuranceType as keyof typeof typeNames]} dari berbagai penyedia
          </TableCaption>
          <TableHeader>
            <TableRow>
              <TableHead className="w-[200px]">Penyedia</TableHead>
              <TableHead>Harga</TableHead>
              <TableHead>Cakupan</TableHead>
              <TableHead>Deductible</TableHead>
              <TableHead>Rating</TableHead>
              <TableHead className="w-[250px]">Manfaat Utama</TableHead>
              <TableHead className="w-[250px]">Pertimbangan</TableHead>
            </TableRow>
          </TableHeader>
          <TableBody>
            {data.map((item, index) => (
              <TableRow key={index} className="hover:bg-muted/50">
                <TableCell className="font-medium">{item.provider}</TableCell>
                <TableCell className="font-semibold text-primary">{item.price}</TableCell>
                <TableCell>{item.coverage}</TableCell>
                <TableCell>{item.deductible}</TableCell>
                <TableCell>
                  <Badge variant="secondary">{item.rating}</Badge>
                </TableCell>
              <TableCell>
                <div className="space-y-1">
                  {item.features.slice(0, 3).map((feature, idx) => (
                    <div key={idx} className="text-sm text-muted-foreground">
                      • {feature}
                    </div>
                  ))}
                </div>
              </TableCell>
              <TableCell>
                <div className="space-y-1">
                  {item.disadvantages.slice(0, 3).map((disadv, idx) => (
                    <div key={idx} className="text-sm text-destructive">
                      • {disadv}
                    </div>
                  ))}
                </div>
              </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </div>
      
      {/* Mobile-friendly card view */}
      <div className="md:hidden space-y-4">
        <h3 className="text-lg font-semibold">Tampilan Mobile</h3>
        {data.map((item, index) => (
          <Card key={index}>
            <CardContent className="p-4 space-y-3">
              <div className="flex justify-between items-start">
                <h4 className="font-semibold">{item.provider}</h4>
                <Badge variant="secondary">{item.rating}</Badge>
              </div>
              <div className="grid grid-cols-2 gap-2 text-sm">
                <div>
                  <span className="text-muted-foreground">Harga:</span>
                  <div className="font-semibold text-primary">{item.price}</div>
                </div>
                <div>
                  <span className="text-muted-foreground">Deductible:</span>
                  <div>{item.deductible}</div>
                </div>
              </div>
              <div>
                <span className="text-muted-foreground text-sm">Cakupan:</span>
                <div className="text-sm">{item.coverage}</div>
              </div>
              <div>
                <span className="text-muted-foreground text-sm">Manfaat:</span>
                <div className="space-y-1 mt-1">
                  {item.features.map((feature, idx) => (
                    <div key={idx} className="text-sm">• {feature}</div>
                  ))}
                </div>
              </div>
              <div>
                <span className="text-muted-foreground text-sm">Pertimbangan:</span>
                <div className="space-y-1 mt-1">
                  {item.disadvantages.map((disadv, idx) => (
                    <div key={idx} className="text-sm">• {disadv}</div>
                  ))}
                </div>
              </div>
            </CardContent>
          </Card>
        ))}
      </div>
    </div>
  );
}
